import { useQueries, useQueryClient } from "@tanstack/react-query"
import { getMetadata } from "../services/getMetadata"
import { filesQueryKeys } from "../utils"

const useMultipleFiles = (fileIds: Array<string | undefined>) => {
  const queryClient = useQueryClient()

  return useQueries({
    queries: fileIds.map(id => ({
      queryKey: filesQueryKeys.metadata(id),
      queryFn: () => getMetadata(id ?? "", queryClient),
      staleTime: 60 * 1000,
      refetchOnWindowFocus: false,
    })),
  })
}

export { useMultipleFiles }
